<template>
  <div class="bda-table" :id="id" ref="bdaTable">
    <div class="content-table">
      <el-table
        class="table"
        v-if="loading "
        :data="tableDealData"
        :height="tableHeight"
        :row-class-name="tabRowClassName"
        border
        align="center"
        :header-cell-style="headStyle"
        style="width: 100%; height: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          v-if="selectWidth !== 0&& columnData.length!==0"
          type="selection"
          :width="selectWidth"
        ></el-table-column>
        <el-table-column
          v-if="indexWidth !== 0&& columnData.length!==0"
          type="index"
          label="序号"
          :width="indexWidth"
        ></el-table-column>

        <el-table-column
          v-for="(item, index) in columnData"
          :key="index"
          :prop="item.prop"
          :label="item.label"
          :width="item.width"
          :align="item.align ? item.align : 'left'"
        >
          <template slot-scope="scope">
            <div v-if="item.prop === 'Operate'" class="operate">
              <el-button
                type="text"
                v-for="(item1, index1) in item.type.filter((e) => {
                return e.disabledValue
                  ? e.disabledValue.indexOf(scope.row[e.disabledField]) === -1
                  : true;
              })"
                :key="index1"
                @click="operate(item1.action, scope.row)"
              >
                <span>{{ item1.name }}</span>
              </el-button>
            </div>
            <div v-if="item.prop === 'Select'" class="select">
              <el-select size="mini" v-model="scope.row[item.model]" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
            </div>
            <div v-if="item.type === 'CilckRow'" class="cilck-row">
              <div class="click-text" @click="clickRow(item.prop, scope.row)">
                <span>{{ scope.row[item.prop] }}</span>
              </div>
            </div>
            <div v-if="item.formatter">
              <span>{{ item.formatter[scope.row[item.prop]] }}</span>
            </div>
            <div
              v-if="item.type !== 'CilckRow' && item.prop !== 'Operate' && item.prop !== 'Select'"
              class="text"
            >
              <span>{{ scope.row[item.prop] }}</span>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      v-if="isPage"
      class="content-pagination"
      @size-change="sizeChange"
      @current-change="currentChange"
      :current-page="queryForm.current"
      :page-sizes="pageConfig.pageSizes"
      :page-size="queryForm.size"
      :layout="pageConfig.layout"
      :total="pageConfig.total"
    ></el-pagination>
  </div>
</template>

<script>
// import Interface from '../base/mixin/talk-manage';
/**
 * @表格
 * @author：risheng.huang
 * @参数说明：
 * tableData:{
 *   tableData: [ //表格数据
        {
          labelName: '潜在过户客户',
          isConfirm: '是',
          confirmTime: '2020-09-02',
        },
      ],
      //表头数据
      columnNameData: [
        { prop: "labelName", label: "标签名称" },
        {
          prop: "Select", label: "是否确认", model: "isConfirm" //选择框绑定字段
        },
        { prop: "confirmTime", label: "确认时间" },
        {
          prop: "Operate", //操作列
          label: "操作",
          align: "center",
          width: "310px",
          type: [
            {
              name: "任务执行配置",
              action: "set",   //执行操作名称
              disabledField: "executionStatus", //限制点击字段
              disabledValue: ["01", "02"] //限制点击字段值
            },
          ]
        }
      ]
 * }
 * headerColor:表头颜色
 * indexWidth:序号列宽度,默认:60,0为不展示
 * selectWidth:选择列宽度.默认:0,0为不展示
 * isPage:是否分页,默认false
 * pageConfig:分页参数
 * 
 * */
export default {
  components: {
  },
  props: {
    tableData: {
      type: Object,
      default: () => {
        return {}
      }
    },
    headerColor: {
      type: String,
      default: () => {
        return ''
      }
    },
    indexWidth: {
      type: Number,
      default: () => {
        return 70
      }
    },
    selectWidth: {
      type: Number,
      default: () => {
        return 0
      }
    },
    isPage: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    pageConfig: {
      type: Object,
      default: () => {
        return {
          name: '',
          current: 1,
          size: 10,
          pageSizes: [10, 20, 30, 40],
          layout: "prev, pager, next, sizes,jumper,total",
          total: 0
        }
      }
    },
  },
  name: "bda-table",
  //   mixins: [Interface],
  data() {
    return {
      id: "bda-table" + Math.random(),
      loading: false,
      //处理过后的表格数据
      tableDealData: [],
      //表头数据
      columnData: [],
      //表格高度
      tableHeight: 0,
      //表头颜色
      headStyle: { background: '#1899fa' },
      //查询
      queryForm: {
        name: '',
        current: 1,
        size: 10,
      },
      //
      options: [
        { name: '是', value: '1' },
        { name: '否', value: '0' },
      ]
    }
  },
  mounted() {
    this.init();
    // this.dom = document.getElementById(this.id)
    window.addEventListener('resize', this.calculateHeight);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.calculateHeight);
  },

  watch: {
    tableData() {
      this.init()
    },
    headerColor() {
      this.headStyle.background = this.headerColor
    },
    pageConfig() {

    }
  },
  methods: {
    //计算高度
    calculateHeight() {
      this.tableHeight = document.getElementById(this.id).clientHeight - (this.isPage ? 52 : 0)
    },
    // 初始化数据
    init() {
      this.queryForm.name = this.pageConfig.name
      this.queryForm.size = this.pageConfig.size
      this.queryForm.current = this.pageConfig.current
      this.headStyle.background = this.headerColor
      this.calculateHeight()
      this.dataDeal(this.tableData)
    },
    //条数改变
    sizeChange(val) {
      this.queryForm.size = val;
      this.$emit('changePage', this.queryForm)
    },
    //页数改变
    currentChange(val) {
      this.queryForm.current = val;
      this.$emit('changePage', this.queryForm)
    },
    //斑马纹样式
    tabRowClassName({ row, rowIndex }) {
      let index = rowIndex + 1;
      if (index % 2 == 0) {
        return 'warning-row'
      }
    },
    // 数据处理
    dataDeal(data) {
      if (Object.keys(data).length !== 0) {
        this.tableDealData = []
        // this.columnData = []
        // console.log(data.columnNameData, 'data.columnNameData');
        this.loading = false
        this.$nextTick(() => {
          this.tableDealData = data.tableData
          this.columnData = data.columnNameData
          this.loading = true
        })
      } else {
        this.loading = true

      }
    },
    //操作
    operate(type, data) {
      this.$emit('operate', type, data)
    },
    //点击表格
    clickRow(prop, data) {
      this.$emit('clickRow', prop, data)
    },
    //选择
    handleSelectionChange(data) {
      this.$emit('select', data)

    },
  },
}
</script>

<style scoped lang="scss">
// 滚动条的宽度
::v-deep .el-table__body-wrapper::-webkit-scrollbar {
  width: 6px; // 横向滚动条
  height: 6px; // 纵向滚动条 必写
}
// 滚动条的滑块
::v-deep .el-table__body-wrapper::-webkit-scrollbar-thumb {
  background-color: #e0e5eb;
  border-radius: 3px;
}
::v-deep .el-table tr th .cell {
  color: #fff;
}
::v-deep.el-table th.gutter {
  display: table-cell !important;
}
::v-deep .el-table td,
.el-table th.is-leaf,
.el-table--border,
.el-table--group {
  border-color: #d9d9d9;
}
::v-deep .el-table--border::after,
.el-table--group::after,
.el-table::before {
  background-color: #d9d9d9;
}
.select {
  ::v-deep .el-input__inner {
    border: 0;
    background-color: transparent;
  }
}
.bda-table {
  width: 100%;
  height: 100%;
  text-align: center;
  .content-table {
    padding: 0 3px;

    .table {
      border: 1px solid #d9d9d9;
    }
  }
}
.click-text {
  color: #1899fa;
  cursor: pointer;
}
.text {
  color: #4d4d4d;
}
.content-pagination {
  margin: 10px 0;
  text-align: center;
}
::v-deep .el-pagination__total {
  margin-left: 10px;
}
</style>