<template>
  <div class="outPowerNotification">
    <div class="content content-border">
      <div class="header">
        <div class="change-type" v-show="false">
          <span
            class="change-button"
            :class="changeTypeActive === '1' ? 'active' : ''"
            @click="changeType('1')"
          >通知内容配置</span>
          <span
            class="change-button"
            :class="changeTypeActive === '2' ? 'active' : ''"
            @click="changeType('2')"
          >停电通知差异化策略库</span>
          <span
            class="change-button"
            :class="changeTypeActive === '3' ? 'active' : ''"
            @click="changeType('3')"
          >计划停电安排优化建议</span>
        </div>
        <!-- :style="changeTypeActive === '1'?{}:{color:'transparent',backgroundColor:'transparent',borderColor:'transparent'}" -->
        <el-button
          v-if="changeTypeActive === '1'"
          type="primary"
          size="mini"
          @click="addConfigurationData"
        >新增</el-button>
      </div>
      <div class="main">
        <template v-if="changeTypeActive === '1'">
          <bdaTable
            :tableData="configurationData"
            :headerColor="'#1899fa'"
            @operate="tableOperate"
            :isPage="true"
            :pageConfig="configurationPageConfig"
            @changePage="changePage"
          ></bdaTable>
        </template>
        <template v-if="changeTypeActive === '2'">
          <bdaTable
            :tableData="strategyData"
            :headerColor="'#1899fa'"
            @operate="tableOperate"
            :isPage="true"
            :pageConfig="strategyPageConfig"
            @changePage="changePage"
          ></bdaTable>
        </template>
        <template v-if="changeTypeActive === '3'">
          <bdaTable
            :tableData="adviceData"
            :headerColor="'#1899fa'"
            @operate="tableOperate"
            :isPage="true"
            :pageConfig="advicePageConfig"
            @changePage="changePage"
          ></bdaTable>
        </template>
      </div>
    </div>
    <el-dialog
      class="add-dialog"
      :visible.sync="addDialogVisible"
      width="80%"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{'新增通知内容' }}</span>
      </span>
      <div class="dialog-content">
        <el-form :model="addDialogForm" label-width="120px">
          <el-row>
            <el-col :span="12">
              <el-form-item label="通知模板名称：" prop="modelName">
                <el-input
                  placeholder="请输入通知模板名称"
                  size="mini"
                  class="input"
                  v-model="addDialogForm.modelName"
                  clearable
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="活动内容：" prop="descibe">
                <el-input
                  class="input-row"
                  type="textarea"
                  :rows="5"
                  :resize="'none'"
                  placeholder="请输入内容"
                  v-model="addDialogForm.descibe"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="分类标签：" prop="classTag">
                <el-input
                  placeholder="请输入通知模板名称"
                  size="mini"
                  class="input"
                  v-model="addDialogForm.classTag"
                  clearable
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveAddDialog">保存</el-button>
        <el-button @click="handleClose">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import bdaTable from "../../components/bda-table/bda-table";
import Interface from '@/base/mixin/power-sensitive-users';
export default {
  name: "outPowerNotification",
  components: {
    bdaTable
  },
  mixins: [Interface],
  props: {},
  data() {
    return {
      //展示列表
      changeTypeActive: "1",
      //   通知内容配置
      configurationData: {},
      configurationQueryForm: {
        modelId: "",
        current: 1,
        size: 10
      },
      //分页设置
      configurationPageConfig: {
        name: "configuration",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      // 停电通知差异化策略库
      strategyData: {},
      strategyQueryForm: {
        current: 1,
        size: 10
      },
      //分页设置
      strategyPageConfig: {
        name: "strategy",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      // 计划停电安排优化建议
      adviceData: {},
      adviceQueryForm: {
        current: 1,
        size: 10
      },
      //分页设置
      advicePageConfig: {
        name: "advice",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //新增模态框
      addDialogVisible: false,
      addDialogForm: {
        modelName: "",
        descibe: "",
        classTag: "",
      }
    };
  },
  mounted() {
    this.changeType('3')
    this.getConfigurationData()
  },
  methods: {
    //切换列表展示
    changeType(val) {
      this.changeTypeActive = val;
      this.configurationQueryForm.current = 1
      this.configurationQueryForm.size = 10
      this.strategyQueryForm.current = 1
      this.strategyQueryForm.size = 10
      this.adviceQueryForm.current = 1
      this.adviceQueryForm.size = 10
      if (this.changeTypeActive === "1") {
        this.getConfigurationData();
      }
      if (this.changeTypeActive === "2") {
        this.getStrategyData();
      }
      if (this.changeTypeActive === "3") {
        this.getAdviceData();
      }
    },
    //点击新增
    addConfigurationData() {
      this.addDialogVisible = true
    },
    //点击保存
    saveAddDialog() {
      this.insertNoticeContent(this.addDialogForm).then(res => {
        this.$message.success(res)
        this.addDialogVisible = false
        this.configurationQueryForm.current = 1
        this.configurationQueryForm.size = 10
        this.getConfigurationData()
      })
    },
    //关闭对话框
    handleClose() {
      this.addDialogVisible = false
    },
    //测试通知内容配置列表
    delConfigurationData() {
      this.deleteNoticeContent({ modelId: this.configurationQueryForm.modelId }).then((res) => {
        this.$message.success(res)
        this.configurationQueryForm.current = 1
        this.configurationQueryForm.size = 10
        this.getConfigurationData()
      })
    },
    //点击表格
    tableOperate(type, data) {
      console.log(type, data);
      if (type === 'delete') {
        this.configurationQueryForm.modelId = data.modelId
        this.delConfigurationData()
      }
    },

    //分页
    changePage(data) {
      console.log(data, "data");
      if (data.name === "configuration") {
        this.configurationQueryForm.size = data.size;
        this.configurationQueryForm.current = data.current;
        this.getConfigurationData();
      }
      if (data.name === "strategy") {
        this.strategyQueryForm.size = data.size;
        this.strategyQueryForm.current = data.current;
        this.getStrategyData();
      }
      if (data.name === "advice") {
        this.adviceQueryForm.size = data.size;
        this.adviceQueryForm.current = data.current;
        this.getAdviceData();
      }
    },
    //获取通知内容配置
    getConfigurationData() {
      this.configurationData = {};
      this.getNoticeContent({
        size: this.configurationQueryForm.size,
        current: this.configurationQueryForm.current
      }).then(res => {
        this.configurationPageConfig.total = res.total;
        this.configurationPageConfig.size = this.configurationQueryForm.size;
        this.configurationPageConfig.current = this.configurationQueryForm.current;
        this.configurationData = {
          tableData: [...res.list],
          columnNameData: [
            { prop: "modelName", label: "通知模板名称" },
            { prop: "descibe", label: "通知内容详细" },
            { prop: "classTag", label: "通知内容分类标签" },
            { prop: "createName", label: "创建人" },
            { prop: "createTime", label: "创建时间" },
            {
              prop: "Operate",
              label: "操作",
              align: "center",
              width: "120px",
              type: [
                {
                  name: "删除",
                  action: "delete"
                }
              ]
            }
          ]
        };
      });
    },
    //获取停电通知差异化策略库
    getStrategyData() {
      this.strategyData = {};
      this.getStrategyList({
        size: this.strategyQueryForm.size,
        current: this.strategyQueryForm.current
      }).then(res => {
        this.strategyPageConfig.total = res.total;
        this.strategyPageConfig.size = this.strategyQueryForm.size;
        this.strategyPageConfig.current = this.strategyQueryForm.current;
        this.strategyData = {
          tableData: [...res.list],
          columnNameData: [
            { prop: "relationNumber", label: "关联关系编号" },
            { prop: "relationName", label: "关联关系名称" },
            { prop: "descibe", label: "关联关系描述" },
            { prop: "weight", label: "权重" },
            { prop: "recResultNumber", label: "推荐结果数量" },
            { prop: "recEmpty", label: "推荐结果为空" },
            { prop: "updateCycle", label: "更新周期" },
            { prop: "createTime", label: "创建时间" },
            { prop: "updateTime", label: "更新时间" },
          ]
        };
      });
    },
    //获取计划停电安排优化建议
    getAdviceData() {
      this.adviceData = {};
      this.getPlannedBlackoutProposal({
        size: this.adviceQueryForm.size,
        current: this.adviceQueryForm.current
      }).then(res => {
        this.advicePageConfig.total = res.total;
        this.advicePageConfig.size = this.adviceQueryForm.size;
        this.advicePageConfig.current = this.adviceQueryForm.current;
        this.adviceData = {
          tableData: [...res.list],
          columnNameData: [
            { prop: "recommendName", label: "建议名称" },
            { prop: "descibe", label: "建议描述" },
            { prop: "createName", label: "创建人" },
            { prop: "createTime", label: "创建时间" },
          ]
        };
      });
    }
  }
};
</script>

<style scoped lang="scss">
.outPowerNotification {
  ::v-deep .el-table .warning-row {
    // background: #e7f6f7;
    background: #e9f3fe;
  }

  .content-border {
    background: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }

  .content {
    height: 500px;
    margin-bottom: 20px;
    .header {
      padding: 0 10px;
      box-sizing: border-box;
    }

    .main {
      height: calc(100% - 68px);
      padding: 10px;
      box-sizing: border-box;
    }
  }

  .change-type {
    width: 100%;
    height: 30px;
    display: flex;
    align-self: start;
    margin: 10px 0 10px;

    .active {
      &.change-button {
        color: #fff;
      }

      background-color: #1899fa;
    }

    .change-button {
      width: 158px;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #1899fa;
      color: #1899fa;
      cursor: pointer;

      &:nth-child(1) {
        border-right: 0;
        border-radius: 4px 0 0 4px;
      }

      &:nth-child(2) {
        border-radius: 0;
      }
      &:nth-child(3) {
        border-left: 0;
        border-radius: 0 4px 4px 0;
      }
    }
  }
  .add-dialog {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .query-group {
      margin-bottom: 10px;
      .search {
        display: flex;
        align-items: center;
      }
    }
    .dialog-table {
      height: 400px;
    }
    .dialog-pagination {
      text-align: center;
      margin: 10px 0;
    }
  }
}
</style>
